import classNames from 'classnames';
import { useState } from 'react';
import styles from './index.less';

interface Menu {
    name: string;
    key: string;
}

interface Props {
    menus: Menu [];
    onSelect: (key:string)=>void
    selectedKey: string;
    disable?:boolean;
    type?: string;
}

const TabMenu = (props: Props) => {
    const { selectedKey, disable, type="m", onSelect, menus } = props;
   
    return (
        <div className={ classNames(styles.container,
            {
                [styles.disableContainer]: disable
            }
            
            )}>{
            menus.map((item, index) => {
                return <div 
                className={
                    classNames(
                        styles.item,
                    {
                        [styles.active]: item.key === selectedKey,
                        [styles.notActive]: item.key !== selectedKey,
                        [styles.m]: type === 'm',
                        [styles.s]: type === 's',
                        [styles.l]: type === 'l'
                    }) }
                    onClick={()=> {
                        if(selectedKey !== item.key && disable === false) {
                            onSelect(item.key)
                        }
                    }}
                    key={item.key}
                    >{item.name}</div>
            } ,[])
            }</div>
    )
}

export default TabMenu;
