import styles from './index.less'
import TabMenu from 'component/TabMenu'
import { useState } from 'react'
const menus = [
    {
        name: '接通率',
        key: 'tong'
    },
    {
        name: '圣城旅',
        key: 'sheng'
    },
    {
        name: '五天苍苍',
        key: 'wu'
    }
]
const TabMenuViewDemo = () => {
    const [key, setKey] = useState('tong')
    return (
        <div className={styles.container}>
            <div>
                <TabMenu
                    menus={menus}
                    selectedKey={key}
                    disable={false}
                    onSelect={(selected) => { setKey(selected)}}
                />
            </div>
            <div className={styles.content}>
                当前选中的 {
                    menus.reduce((result:string, menu:any) => {
                        if(result !== '') return result;
                        if(menu.key === key) return menu.name;
                        return ''
                    }, '')
                }
            </div>
        </div>
    )
}

export default TabMenuViewDemo